body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-y: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
h1 {
  font-size: 2rem;
  margin: 0;
}
h2 {
  font-size: 1.5rem;
  margin: 0;
}
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: url("https://images.pexels.com/photos/912110/pexels-photo-912110.jpeg?cs=srgb&dl=atmosphere-ciel-ciel-bleu-lumiere-du-jour-912110.jpg&fm=jpg")
    no-repeat center center fixed;
  background-size: cover;
  color: white;
  text-align: center;
  padding: 20px;
 
}
.App h1 {
  font-size: 3rem;
  margin-bottom: 5px;
}
.weather {
  background: rgba(255, 255, 255, 0.466);
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(10px);
  width: 400px;
  max-width: 90%;
  margin: 20px;
  font-size: 1.4rem;
  color: #333;
  transition: all 0.3s ease;
}
.weather:hover {
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}
.weather h2 {
  margin-bottom: 10px;
  font-size: 2rem;
  color: #333;
  font-weight: bold;
}
.weather p {
  margin: 25px 5px;
  font-size: 1.2rem;
  color: #555;
  font-weight: normal;
}
.weather img {
  width: 150px;
  height: 150px;
  margin-right: 10px;
  border-radius: 50%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
  max-width: 100%;
  margin: 0 auto;
  display: block;
}
.weather img:hover {
  transform: scale(1.1);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}
.weather input {
  padding: 10px;
  border: none;
  border-radius: 5px;
  margin-top: 10px;
  font-size: 1rem;
  width: 100%;
  max-width: 300px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: all 0.3s ease;
}
.weather input:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}
